import Vue from "vue";
import vueRouter from 'vue-router'

Vue.use(vueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: () => import('@/views/login.vue')
  },
  {
    path: '/layout',
    component: () => import('@/views/layout.vue'),
    children: [
      {
        path: '/',
        name: 'home',
        component: () => import('@/views/home.vue')
      },
      {
        path: '/layout/data',
        name: 'data',
        component: () => import('@/views/data_monitoring.vue')
      },
      {
        path: '/layout/equipment',
        name: 'equipment',
        component: () => import('@/views/equipment_monitoring.vue')
      },
      {
        path: '/username',
        name: 'username',
        component: () => import('@/views/username.vue')
      },
    ]
  },
]

const router = new vueRouter({ routes })

