<template>
  <div
    :id="option_id"
    :style="{
      width: option_width,
      height: option_height,
    }"
  ></div>
</template>

<script>
import * as echarts from "echarts";
export default {
  props: ["option_width", "option_height", "option_otn", "option_id"],
  mounted() {
    var myChart = echarts.init(document.getElementById(this.option_id));
    myChart.setOption(this.option_otn);
  },
};
</script>

