//login.js
import request from '@/utils/requests.js'
import qs from 'qs'
import axios from 'axios'


export function doLogin(username, password) {
  let data = {
    username,
    password
  }
  data = qs.stringify(data)
  return request({
    url: '/login',
    method: 'post',
    data
  })
}
// 注册函数
export function register(username, password) {
  let data = {
    username,
    password
  }
  data = qs.stringify(data)
  return request({
    url: '/register',
    method: 'post',
    data
  })
}

export function selectData(token) {
  let data = {
    token
  }
  data = qs.stringify(data)
  return request({
    url: '/register',
    method: 'post',
    data
  })
}


